    export default {
        data() {
            return {
                company_name: "厦门夕阳红酒吧",  // 公司名称
               // 轮播图列表
                bannerList: [
                    { img:require("@/components/manage/images/banner.png") }
                ],
                bannerIndex: 0,   // 当前轮播图是第几项
                show: false,  // 判断选择门店弹窗显示或隐藏
                shopName: ["厦门夕阳红酒吧","福州夕阳红酒吧","泉州夕阳红酒吧"],
                await: 0,  // 等待审批次数
                turn: 3,  //  出勤天数
                // 常用功能列表
                commonList: [
                    {icon:require("@/components/manage/images/shenpi.png"),txt:"审批中心"},
                    {icon:require("@/components/manage/images/zhinengbaobiao.png"),txt:"智能报表",to:"/statistics/serve"},
                    {icon:require("@/components/manage/images/kaoqindaka.png"),txt:"考勤打卡",to:"/check"},
                    {icon:require("@/components/manage/images/kehujianyi.png"),txt:"客户建议",to:"/manage/suggest"}
                ],
                // 快捷功能列表
                shortcutList: [
                    {icon:require("@/components/manage/images/chucha.png"),txt:"出差",to:"/evection"},
                    {icon:require("@/components/manage/images/buka.png"),txt:"补卡",to:"/check/month"},
                    {icon:require("@/components/manage/images/jiaban.png"),txt:"加班",to:"/manage/overtime"},
                    {icon:require("@/components/manage/images/qingshen.png"),txt:"申请记录",to:'/check/apply/record'},
                    {icon:require("@/components/manage/images/baoxiao.png"),txt:"报销"},
                    {icon:require("@/components/manage/images/beiyongjin.png"),txt:"备用金"},
                    {icon:require("@/components/manage/images/fukuanshenqing.png"),txt:"付款申请"},
                    {icon:require("@/components/manage/images/bufapiao.png"),txt:"补发票"},
                ],
            }
        },
        methods:{
            awaita(index){
                if( index > 3 ){
                    this.$vux.toast.text('此功能暂未开放', 'middle')
                }
            },
//            选择门店
            shopNames(){
                console.log("我要选择门店了")
                this.show = true;
            },
//            选择门店弹窗确定按钮
            choice(){
                console.log("门店选好了,我可能要请求数据了")
                this.show = false;
            },
        }
    }



